import React, { useContext, useState } from 'react';
import { Box, Button, IconButton, Menu, MenuItem, Typography } from '@mui/material';
import "./style.scss";
import LogoutIcon from '@mui/icons-material/Logout';
import CenterBox from './../CenterBox/index';
import AccountBalanceWalletIcon from '@mui/icons-material/AccountBalanceWallet';
import { AppContext } from '../..';
import { formatAddress } from '../../utils';
import toast from 'react-hot-toast';

export default function WalletButton({ connectWallet }) {
    const { address, setAddress, amountToken } = useContext(AppContext)

    const onLogout = (e) => {
        e.stopPropagation();
        localStorage.removeItem('address');
        setAddress(null);
    }

    const onCopy = (e) => {
        e.stopPropagation();
        navigator.clipboard.writeText(address);
        toast.success("Copied");
    }

    return (
        <Box className="wallet-container">
            {!address ?
                <Button className="btn-connect-wallet">
                    <AccountBalanceWalletIcon fontSize='small' />
                    <Typography onClick={connectWallet}>Connect Wallet</Typography>
                </Button> :
                <Box className="account-container">
                    <CenterBox>
                        <Typography className='hold-amount'>{amountToken || "0.0"} AAX</Typography>
                    </CenterBox>
                    <Button className="account-wrapper" onClick={onCopy}>
                        <Typography className='address'>{formatAddress(address)}</Typography>
                        <IconButton className="logout" onClick={onLogout}>
                            <LogoutIcon />
                        </IconButton>
                    </Button>
                </Box>
            }
        </Box>
    )
}
