import React, { useContext, useState } from 'react';
import { Box, IconButton, Menu, MenuItem, Typography } from '@mui/material';
import Image from '../../components/Image';
import "./style.scss";
import { Link } from 'react-router-dom';
import Hamburger from 'hamburger-react';
import WalletButton from '../WalletButton';
import Logo from "../../assets/images/logo.png";
import LoadingModal from '../LoadingModal';
import { createWebsocket, isMobile } from '../../utils';
import { AppContext } from '../..';
import toast from 'react-hot-toast';

export default function Appbar({ title, description }) {
    const [anchorEl, setAnchorEl] = useState(null);
    const open = Boolean(anchorEl);
    const { setAddress } = useContext(AppContext);
    const defaultSteps = [
        {
            label: 'Singing Request',
            subtitle: "Sign a transaction to connect a wallet",
            description: `This will generate QR code to sign a transaction. Please scan the QR code with your XUMM app.`,
            qr: true,
        },
        {
            label: 'Collecting sign information',
            description: `We are collecting sign transaction payload information. Please wait...`,
            tx: true,
        },
        {
            label: 'Verifying signed data',
            description: `You signed successfully. Please wait a moment to verify your signature.`,
        },
    ];
    const [steps, setSteps] = useState(defaultSteps);
    const [currentStep, setCurrentStep] = useState(0);
    const [openLoading, setOpenLoading] = useState(false);

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    const connectWallet = async () => {
        try {
            setCurrentStep(0);
            setOpenLoading(true);

            const mobile = isMobile();

            const res = await fetch(`${process.env.REACT_APP_SERVER_URL}/signin-xumm?isMobile=${mobile}`);
            const data = await res.json();

            if (!data) return;

            const payload = data.result;

            if (payload) {
                if (mobile) {
                    window.open(payload.next.always, "_blank");
                } else {
                    console.log("***", payload.refs.qr_png)
                    const _steps = [...steps];
                    _steps[0].qr_link = payload.refs.qr_png;
                    setSteps(_steps);
                }

                const ws_url = payload.refs.websocket_status;
                const webs = createWebsocket(ws_url);

                webs.onmessage = async (e) => {
                    const event = JSON.parse(e.data);

                    if (event.signed) {
                        const payloadUuid = event.payload_uuidv4;
                        try {
                            const _steps = [...steps];
                            _steps[1].subtitle = payloadUuid;
                            setSteps(_steps);
                            setCurrentStep(1);

                            const check = await fetch(`${process.env.REACT_APP_SERVER_URL}/get-payload?uuid=${payloadUuid}`);
                            const checkData = await check.json();
                            const hex = checkData.result.response.hex;

                            setCurrentStep(2);
                            const checkSign = await fetch(`${process.env.REACT_APP_SERVER_URL}/check-sign?hex=${hex}`);
                            const checkSignData = await checkSign.json();
                            console.log("checkSignData: ", checkSignData);

                            if ("xrpAddress" in checkSignData.result) {
                                localStorage.setItem("address", checkSignData.result.xrpAddress);
                                setAddress(checkSignData.result.xrpAddress);
                            }
                        } catch (error) {
                            console.log(error);
                            toast.error(error.message);
                        } finally {
                            setOpenLoading(false);
                        }
                    }
                };
            } else {
                toast.error("Something went wrong. Please try again later.");
                setOpenLoading(false);
            }
        } catch (error) {
            console.log(error);
            toast.error(error.message);
            setOpenLoading(false);
        }
    };

    return (
        <>
            <LoadingModal open={openLoading} steps={steps} currentStep={currentStep} />
            <Box className="appbar-container">
                <Box className="mobile-menu">
                    <IconButton
                        id="basic-button"
                        className="menu-icon"
                        aria-controls={open ? 'basic-menu' : undefined}
                        aria-haspopup="true"
                        aria-expanded={open ? 'true' : undefined}
                        onClick={handleClick}
                    >
                        <Hamburger toggled={open} size={20} />
                    </IconButton >
                    <Menu
                        className="mobile-menu"
                        anchorEl={anchorEl}
                        open={open}
                        onClose={handleClose}
                        MenuListProps={{
                            'aria-labelledby': 'basic-button',
                        }}
                    >
                        <MenuItem>
                            <Image className="mobile-logo" href="/" src={Logo} />
                        </MenuItem>
                        <MenuItem onClick={handleClose}><Link to="/" >AAX Staking</Link></MenuItem>
                        <MenuItem onClick={handleClose}><Link to="/stakelog" >My Staking</Link></MenuItem>
                        <MenuItem onClick={handleClose}><Link to="/swap" >Swap</Link></MenuItem>
                    </Menu>
                </Box>
                <Box className="page-title" sx={{ flexGrow: 1 }}>
                    <Typography className='title' variant="h6" component="div">
                        {title}
                    </Typography>
                    <Typography className='description' component="div">
                        {description}
                    </Typography>
                </Box>
                <WalletButton connectWallet={connectWallet} />
            </Box>
            <Box className="mobile-page-title">
                <Typography className='title' variant="h6" component="div">
                    {title}
                </Typography>
                <Typography className='description' component="div">
                    {description}
                </Typography>
            </Box>
        </>
    )
}
