import React, { useContext, useEffect, useState } from 'react'
import "./style.scss";
import { Box, Button, Container, Grid, IconButton, Input, List, ListItem, TextField, Typography } from '@mui/material';
import DashboardLayout from '../../components/DashboardLayout';
import PersonIcon from '@mui/icons-material/Person';
import toast from 'react-hot-toast';
import { createWebsocket, formatAddress, isMobile } from '../../utils';
import { AppContext } from '../..';
import LoadingModal from '../../components/LoadingModal';

export default function Home() {
  const [amount, setAmount] = useState();
  const { address, amountToken, stakeOptions } = useContext(AppContext);
  const defaultSteps = [
    {
      label: 'Payment to escrow address',
      subtitle: `To: ${process.env.REACT_APP_XRP_TREASURY_ADDRESS}`,
      description: `Send your payment to the escrow address. This is the first step of staking. Please check the escrow address.`,
      qr: true,
    },
    {
      label: 'Verifying your transaction',
      description: `Thanks for your payment. We are verifying your transaction. Once transaction is approved, your stake will be regsitered in our staking system. Please wait...`,
      tx: true
    },
    {
      label: 'Now staking...',
      description: `You signed successfully. Please wait a moment to stake your funds.`,
    },
  ];
  const [steps, setSteps] = useState(defaultSteps);
  const [currentStep, setCurrentStep] = useState(0);
  const [openLoading, setOpenLoading] = useState(false);
  const [options, setOptions] = useState([]);

  useEffect(() => {
    setOptions(stakeOptions);
  }, [stakeOptions])

  const closeLoadingModal = () => {
    setOpenLoading(false);
  }

  const onAmountChange = (e) => {
    const value = e.target.value;
    setOptions(prev => prev.map(option => {
      option.receive = (value * (1 + option.reward_percentage / 100) * 100).toFixed(0) / 100;
      return option;
    }));

    setAmount(value);
  }

  const onMaxClick = () => {
    setAmount(amountToken);
  }

  const onOptionSelected = (index) => {
    setOptions(prev => prev.map((option, i) => {
      option.selected = i === index;
      return option;
    }));
  }

  const stakeFunds = async () => {
    try {
      setCurrentStep(0);
      setSteps(defaultSteps);
      setOpenLoading(true);

      const mobile = isMobile();

      const selectStake = stakeOptions.find(option => option.selected);
      if (!selectStake) {
        closeLoadingModal()
        toast.error("Please select a stake option");
        return;
      }

      const res = await fetch(`${process.env.REACT_APP_SERVER_URL}/stake/payload`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({
          address: address,
          amount: amount,
          option: selectStake.id,
          isMobile: mobile
        })
      });
      const data = await res.json();

      if (!data) {
        closeLoadingModal();
        toast.error("Server response is invalid. Please try again later.");
        return;
      }

      const payload = data.result;

      if (payload) {
        if (mobile) {
          window.open(payload.next.always, "_blank");
        } else {
          const _steps = [...steps];
          _steps[0].qr_link = payload.refs.qr_png;
          setSteps(_steps);
        }

        const ws_url = payload.refs.websocket_status;
        const webs = createWebsocket(ws_url);

        webs.onmessage = async (e) => {
          const event = JSON.parse(e.data);

          if (event.signed) {
            const payloadUuid = event.payload_uuidv4;
            try {
              const _steps = [...steps];
              _steps[1].subtitle = event.txid;
              setSteps(_steps);
              setCurrentStep(1);

              const check = await fetch(`${process.env.REACT_APP_SERVER_URL}/get-payload?uuid=${payloadUuid}`);
              const checkData = await check.json();
              const hex = checkData.result.response.hex;

              const checkSign = await fetch(`${process.env.REACT_APP_SERVER_URL}/check-sign?hex=${hex}`);
              const checkSignData = await checkSign.json();

              if (checkSignData.result.xrpAddress === address) {
                setCurrentStep(2);

                await fetch(`${process.env.REACT_APP_SERVER_URL}/stake`, {
                  method: "POST",
                  headers: {
                    "Content-Type": "application/json"
                  },
                  body: JSON.stringify({
                    txid: event.txid,
                    address: address,
                    amount: amount,
                    option: selectStake.id
                  })
                });

                toast.success("Staking is completed successfully");
                setAmount("");
                setOptions(prev => prev.map(option => {
                  option.selected = false;
                  return option;
                }));

              } else {
                toast.error("Invalid signature");
                closeLoadingModal();
              }

            } catch (error) {
              console.log(error);
              toast.error(error.message);
            } finally {
              closeLoadingModal();
            }
          } else if ("signed" in event && event.signed === false) {
            toast.error("Transaction is cancelled");
            closeLoadingModal();
          }
        };
      } else {
        toast.error("Something went wrong. Please try again later.");
        closeLoadingModal();
      }
    } catch (error) {
      closeLoadingModal()
      toast.error(error.message);
      return;
    }

  }

  const onStake = async () => {
    if (!address) {
      toast.error("Please connect your wallet");
      return;
    }

    if (!amount) {
      toast.error("Please enter amount");
      return;
    }

    if (isNaN(parseFloat(amount)) || !isFinite(amount)) {
      toast.error("Amount is invalid");
      return;
    }

    if (amount < 100) {
      toast.error("Minimum amount to stake is 100");
      return;
    }

    if (amountToken - amount < 0) {
      toast.error("Insufficient balance");
      return;
    }

    const selectedOption = stakeOptions.find(option => option.selected);
    if (!selectedOption) {
      toast.error("Please select a stake option");
      return;
    }

    await stakeFunds();
  }

  return (
    <DashboardLayout className="home-box">
      <LoadingModal open={openLoading} steps={steps} currentStep={currentStep} />
      <Box className="card">
        <Box className="card-header">
          <Box>
            <Typography className='amount-label'>Available to Stake</Typography>
            <Typography className='amount'>{amountToken} AAX</Typography>
          </Box>
          <Box className="account-container">
            <Typography className='address'>{formatAddress(address)}</Typography>
            <IconButton className="avatar" size='small'>
              <PersonIcon />
            </IconButton>
          </Box>
        </Box>
        <Box className="card-body">
          <Box className="input-group">
            <TextField value={amount} variant="outlined" placeholder="Amount to stake (min: 100)" onChange={onAmountChange} />
            <Button className="btn-max" onClick={onMaxClick}>MAX</Button>
          </Box>
          {options.map((option, index) => (
            <Grid container className={`stake-option ${option.selected ? "selected" : ""}`} key={option.duration} onClick={() => onOptionSelected(index)}>
              <Grid item xs={4}>
                <Typography className='column-title'>Months</Typography>
                <Typography className='column-value'>{option.duration ?? 0} Month{option.duration === 1 ? "" : "s"}</Typography>
              </Grid>
              <Grid item xs={4}>
                <Typography className='column-title'>Reward</Typography>
                <Typography className='column-value'>{option.reward_percentage ?? 0}%</Typography>
              </Grid>
              <Grid item xs={4}>
                <Typography className='column-title'>Receive</Typography>
                <Typography className='column-value'>{option.receive ?? 0} AAX</Typography>
              </Grid>
            </Grid>
          ))}
          <Button className="btn-stake" onClick={onStake}>Submit</Button>
        </Box>
      </Box>
    </DashboardLayout >
  )
}
