import React, { useContext, useEffect, useState } from 'react'
import "./style.scss";
import { Box, Button, Container, Grid, IconButton, Input, List, ListItem, TextField, Typography } from '@mui/material';
import DashboardLayout from '../../components/DashboardLayout';
import PersonIcon from '@mui/icons-material/Person';
import toast from 'react-hot-toast';
import { createWebsocket, formatAddress, isMobile } from '../../utils';
import { AppContext } from '../..';
import LoadingModal from '../../components/LoadingModal';

export default function Swap() {
  const [amount, setAmount] = useState();
  const [oldTokenAmount, setOldTokenAmount] = useState(0);
  const { address } = useContext(AppContext);
  const defaultSteps = [
    {
      label: 'Check trustline',
      description: `Checking the new AAX token Trustline. Please wait...`,
      qr: true,
    },
    {
      label: 'Payment to escrow address',
      subtitle: `To: ${process.env.REACT_APP_XRP_TREASURY_ADDRESS}`,
      description: `Send your payment to the escrow address. Please confirm the address.`,
      qr: true,
    },
    {
      label: 'Verifying your transaction',
      description: `Thanks for your payment. We are verifying your transaction. Once transaction is approved, your token will be swaped with a new token. Please wait...`,
      tx: true
    },
    {
      label: 'Now swaping...',
      description: `You signed successfully. Please wait a moment while sending you tokens.`,
    },
  ];
  const trustlineSteps = [
    {
      label: 'Check trustline',
      description: `Checking the new AAX token Trustline. Please wait...`,
      qr: true,
    },
  ];
  const [steps, setSteps] = useState(defaultSteps);
  const [currentStep, setCurrentStep] = useState(0);
  const [openLoading, setOpenLoading] = useState(false);

  useEffect(() => {
    getOldTokenAmount();
  }, [])

  const closeLoadingModal = () => {
    setOpenLoading(false);
  }

  const onAmountChange = (e) => {
    const value = e.target.value;
    setAmount(value);
  }

  const onMaxClick = () => {
    setAmount(oldTokenAmount);
  }

  const getOldTokenAmount = async () => {
    try {
      const res = await fetch(`${process.env.REACT_APP_SERVER_URL}/account/old?address=${address}`);
      const data = await res.json();

      if (data.status !== 200) {
        toast.error(data.error);
        return;
      }

      setOldTokenAmount(data.result.balance);
    } catch (error) {
      toast.error("Can't read account balance.");
      console.error(error);
    }
  }

  const setTrustLine = async (callback) => {
    try {
      const mobile = isMobile();

      const res = await fetch(`${process.env.REACT_APP_SERVER_URL}/trustline`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({
          address: address,
          isMobile: mobile
        })
      });
      const data = await res.json();

      if (!data) {
        closeLoadingModal();
        toast.error("Can't set trustline. Please try again later.");
        return;
      }

      const payload = data.result;

      if (payload) {
        if (mobile) {
          window.open(payload.next.always, "_blank");
        } else {
          const _steps = [...steps];
          _steps[0].qr_link = payload.refs.qr_png;
          setSteps(_steps);
        }

        const ws_url = payload.refs.websocket_status;
        const webs = createWebsocket(ws_url);

        webs.onmessage = async (e) => {
          const event = JSON.parse(e.data);

          if (event.signed) {
            callback();
          } else if ("signed" in event && event.signed === false) {
            toast.error("Transaction is cancelled");
            closeLoadingModal();
          }
        };
      } else if (payload === null) {
        callback();
      } else {
        toast.error("Something went wrong. Please try again later.");
        closeLoadingModal();
      }
    } catch (error) {
      closeLoadingModal()
      toast.error(error.message);
      return;
    }
  }

  const swap = async () => {
    try {
      setCurrentStep(0);
      setSteps(defaultSteps);
      setOpenLoading(true);

      const mobile = isMobile();

      await setTrustLine(async () => {
        setCurrentStep(1);

        const res = await fetch(`${process.env.REACT_APP_SERVER_URL}/swap/payload`, {
          method: "POST",
          headers: {
            "Content-Type": "application/json"
          },
          body: JSON.stringify({
            address: address,
            amount: amount,
            isMobile: mobile
          })
        });
        const data = await res.json();

        if (!data) {
          closeLoadingModal();
          toast.error("Server response is invalid. Please try again later.");
          return;
        }

        const payload = data.result;

        if (payload) {
          if (mobile) {
            window.open(payload.next.always, "_blank");
          } else {
            const _steps = [...steps];
            _steps[1].qr_link = payload.refs.qr_png;
            setSteps(_steps);
          }

          const ws_url = payload.refs.websocket_status;
          const webs = createWebsocket(ws_url);

          webs.onmessage = async (e) => {
            const event = JSON.parse(e.data);

            if (event.signed) {
              const payloadUuid = event.payload_uuidv4;
              try {
                const _steps = [...steps];
                _steps[2].subtitle = event.txid;
                setSteps(_steps);
                setCurrentStep(2);

                const check = await fetch(`${process.env.REACT_APP_SERVER_URL}/get-payload?uuid=${payloadUuid}`);
                const checkData = await check.json();
                const hex = checkData.result.response.hex;

                const checkSign = await fetch(`${process.env.REACT_APP_SERVER_URL}/check-sign?hex=${hex}`);
                const checkSignData = await checkSign.json();

                if (checkSignData.result.xrpAddress === address) {
                  setCurrentStep(3);

                  await fetch(`${process.env.REACT_APP_SERVER_URL}/swap`, {
                    method: "POST",
                    headers: {
                      "Content-Type": "application/json"
                    },
                    body: JSON.stringify({
                      txid: event.txid,
                      address: address,
                      amount: amount
                    })
                  });

                  toast.success("Swap is completed successfully");
                  setAmount("");
                } else {
                  toast.error("Invalid signature");
                  closeLoadingModal();
                }

              } catch (error) {
                console.log(error);
                toast.error(error.message);
              } finally {
                closeLoadingModal();
              }
            } else if ("signed" in event && event.signed === false) {
              toast.error("Transaction is cancelled");
              closeLoadingModal();
            }
          };
        } else {
          toast.error("Something went wrong. Please try again later.");
          closeLoadingModal();
        }
      });
    } catch (error) {
      closeLoadingModal()
      toast.error(error.message);
      return;
    }
  }

  const onSwap = async () => {
    if (!address) {
      toast.error("Please connect your wallet");
      return;
    }

    if (!amount) {
      toast.error("Please enter amount");
      return;
    }

    if (amount < 1) {
      toast.error("Minimum amount to swap is 1");
      return;
    }

    if (oldTokenAmount - amount < 1) {
      toast.error("Insufficient balance");
      return;
    }

    await swap();
  }

  const onTrustline = async () => {
    setCurrentStep(0);
    setSteps(trustlineSteps);
    setOpenLoading(true);

    await setTrustLine(() => {
      toast.success("Trustline is set successfully");
      setOpenLoading(false);
    });
  }

  return (
    <DashboardLayout className="home-box">
      <LoadingModal open={openLoading} steps={steps} currentStep={currentStep} />
      <Box className="card">
        <Box className="card-header">
          <Box>
            <Typography className='amount-label'>Available to Swap</Typography>
            <Typography className='amount'>{oldTokenAmount} AAX (Old)</Typography>
          </Box>
          <Button className="btn-trustline" onClick={onTrustline}>
            Set Trustline
          </Button>
        </Box>
        <Box className="card-body">
          <Box className="input-group">
            <TextField value={amount} variant="outlined" placeholder="Amount to stake (min: 100)" onChange={onAmountChange} />
            <Button className="btn-max" onClick={onMaxClick}>MAX</Button>
          </Box>
          <Button className="btn-stake" onClick={onSwap}>Swap</Button>
        </Box>
      </Box>
    </DashboardLayout >
  )
}
