import React, { useEffect, useState } from 'react';
import * as ReactDOM from "react-dom/client";
import { BrowserRouter as Router, Routes, Route, Navigate } from "react-router-dom";
import reportWebVitals from './reportWebVitals';
import './index.css';
import "./global.scss";
import Home from "./pages/home";
import { Toaster, toast } from 'react-hot-toast';
import { getAddress } from './utils';
import StakeLog from './pages/stakelog';
import Swap from './pages/swap';

export const AppContext = React.createContext();

function AppProvider({ children }) {
  const [address, setAddress] = useState(getAddress);
  const [amountToken, setAmountToken] = useState(0);
  const [stakeOptions, setStakeOptions] = useState([
    {
      months: 1,
      reward: 0.5,
      selected: false
    },
    {
      months: 3,
      reward: 2,
      selected: false
    },
    {
      months: 6,
      reward: 4.5,
      selected: false
    },
    {
      months: 12,
      reward: 10,
      selected: false
    },
  ]);

  const getTokenAmount = async (address) => {
    try {
      const res = await fetch(`${process.env.REACT_APP_SERVER_URL}/account?address=${address}`);
      const data = await res.json();

      if (data.status !== 200) {
        toast.error(data.error);
        return;
      }

      setAmountToken(data.result.balance);
    } catch (error) {
      toast.error("Can't read account balance.");
      console.error(error);
    }
  }

  const getStakeOptions = async () => {
    try {
      const res = await fetch(`${process.env.REACT_APP_SERVER_URL}/stake/options`);
      const data = await res.json();

      if (data.status !== 200) {
        toast.error(data.error);
        return;
      }
      setStakeOptions(data.result);
    } catch (error) {
      console.error(error);
      toast.error("Can't read stake options");
    }
  }

  useEffect(() => {
    getStakeOptions();
  }, [])

  useEffect(() => {
    address && getTokenAmount(address);
  }, [address]);

  return (
    <AppContext.Provider value={{
      address,
      setAddress,
      amountToken,
      setAmountToken,
      stakeOptions,
      setStakeOptions
    }}>
      {children}
      <Toaster />
    </AppContext.Provider>
  );
}

ReactDOM.createRoot(document.getElementById("root")).render(
  <React.StrictMode>
    <AppProvider>
      <Router>
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/stakelog" element={<StakeLog />} />
          <Route path="/swap" element={<Swap />} />
        </Routes>
      </Router>
    </AppProvider>
  </React.StrictMode>
);

reportWebVitals();
